<template>
    <div>
        <DebtCalendar />
        <CRow class="p-2">
            <CCol sm="9">
                <CInput
                    size="sm"
                    v-model="searchText"
                    v-on:keyup.enter="search"
                >
                    <template #prepend>
                        <CButton color="primary" size="sm" v-on:click="search">
                            <CIcon name="cil-magnifying-glass" size="sm" />
                        </CButton>
                    </template>
                </CInput>
            </CCol>
            <CCol sm="3" class="text-right">
                <CButton
                    color="primary"
                    size="sm"
                    v-if="$store.getters.isAdmin"
                    v-on:click="add"
                >
                    <CIcon name="cil-plus" size="sm" />
                </CButton>
                <CButton color="primary" size="sm" v-on:click="download">
                    <CIcon name="cil-cloud-download" size="sm" />
                </CButton>
                <CButton color="primary" size="sm" v-on:click="reload">
                    <CIcon name="cil-reload" size="sm" />
                </CButton>
            </CCol>
        </CRow>
        <v-data-table
            class="elevation-1"
            :page="page"
            :pageCount="pageCount"
            :headers="headers"
            :items="items"
            :options.sync="options"
            :server-items-length="serverItemsLength"
            :loading="loading"
            :sort-by.sync="sortBy"
            :sort-desc.sync="sortDesc"
            :footer-props="{
                disableItemsPerPage: disableItemsPerPage,
                disablePagination: disablePagination,
                showFirstLastPage: true,
                showCurrentPage: true,
                itemsPerPageOptions: [10, 20, 50, 100]
            }"
        >
            <template v-slot:[`item.debt_date`]="{ item }">
                <div v-if="item.debt_date">
                    {{ item.debt_date | moment("dddd, Do MMMM YYYY") }}
                </div>
            </template>
            <template v-slot:[`item.due_date`]="{ item }">
                <div v-if="item.due_date">
                    {{ item.due_date | moment("dddd, Do MMMM YYYY") }}
                </div>
            </template>
            <template v-slot:[`item.paid_date`]="{ item }">
                <div v-if="item.paid_date">
                    {{ item.paid_date | moment("dddd, Do MMMM YYYY") }}
                </div>
            </template>
            <template v-slot:[`item.created_at`]="{ item }">
                {{ item.created_at | moment("dddd, Do MMMM YYYY") }}
            </template>
            <template v-slot:[`item.updated_at`]="{ item }">
                {{ item.updated_at | moment("dddd, Do MMMM YYYY") }}
            </template>
            <template v-slot:[`item.actions`]="{ item }">
                <CButtonGroup>
                    <CButton
                        v-for="action in item.actions"
                        :key="action.key"
                        :color="action.color"
                        :disabled="action.disabled"
                        size="sm"
                        @click="click(item.id, action.type)"
                    >
                        {{ action.title }}
                    </CButton>
                </CButtonGroup>
            </template>
            <template slot="body.append">
                <tr>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th>Debt Amount</th>
                    <th>{{ debtAmount }}</th>
                    <th>{{ paidAmount }}</th>
                    <th></th>
                </tr>
                <tr>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th>Debt Total</th>
                    <th>{{ totalAmount }}</th>
                    <th></th>
                    <th></th>
                </tr>
            </template>
        </v-data-table>
    </div>
</template>

<script>
import { mapState } from "vuex";
import DebtCalendar from "./componets/DebtCalendar";

export default {
    name: "Debt",
    components: {
        DebtCalendar
    },
    computed: {
        ...mapState(["debt"]),
        serverItemsLength() {
            return this.debt.data?.total;
        },
        pageCount() {
            return this.debt.data?.last_page;
        },
        items() {
            return this.debt.data?.data;
        }
    },
    data() {
        return {
            searchText: null,
            page: 1,
            loading: false,
            options: {},
            sortBy: "created_at",
            sortDesc: false,
            disableItemsPerPage: false,
            disablePagination: false,
            headers: [
                { text: "User", value: "user.name" },
                { text: "Item", value: "item" },
                { text: "Amount", value: "amount" },
                { text: "Paid", value: "paid" },
                { text: "Debt Date", value: "debt_date" },
                { text: "Due Date", value: "due_date" },
                { text: "Paid Date", value: "paid_date" },
                { text: "Created at", value: "created_at" },
                { text: "Updated at", value: "updated_at" },
                { text: "Actions", value: "actions" }
            ],
            debtAmount: 0,
            paidAmount: 0,
            totalAmount: 0
        };
    },
    watch: {
        options: {
            handler() {
                this.fetch();
            }
        },
        loading() {
            this.disableItemsPerPage = this.loading;
            this.disablePagination = this.loading;
        },
        searchText(val) {}
    },
    mounted() {
        this.fetch();
    },
    methods: {
        fetch() {
            let self = this;
            self.loading = true;
            const { page, itemsPerPage, sortBy, sortDesc } = self.options;
            let data = {
                page: page,
                per_page: itemsPerPage,
                sort_by: sortBy,
                sort_desc: sortDesc,
                search: self.searchText
            };
            this.$store
                .dispatch("debt/get", data)
                .then(response => {
                    self.loading = false;
                    self.debtAmount = response.data.debt_amount;
                    self.paidAmount = response.data.paid_amount;
                    self.totalAmount = response.data.total_amount;
                })
                .catch(error => {
                    self.loading = false;
                });
        },
        search() {
            this.fetch();
        },
        add() {
            this.$router.push({ name: "CreateDebt" });
        },
        download() {
            let self = this;
            self.loading = true;
            const { page, itemsPerPage, sortBy, sortDesc } = self.options;
            let data = {
                page: page,
                per_page: itemsPerPage,
                sort_by: sortBy,
                sort_desc: sortDesc,
                search: self.searchText
            };
            this.$store
                .dispatch("debt/export", data)
                .then(response => {
                    self.loading = false;
                })
                .catch(error => {
                    self.loading = false;
                });
        },
        reload() {
            this.fetch();
        },
        async click(id, type) {
            switch (type) {
                case "RouterPush":
                    this.$router.push({
                        name: "DebtDetails",
                        params: { id: id }
                    });
                    break;
                case "Delete":
                    if (
                        await this.$refs.dialog.open(
                            "Confirm",
                            "Are you sure you want to delete this record?"
                        )
                    ) {
                        let self = this;
                        this.$store
                            .dispatch("debt/delete", { id: id })
                            .then(response => {
                                self.loading = false;
                                self.fetch();
                            })
                            .catch(error => {
                                self.loading = false;
                            });
                    }
                    break;
            }
            // console.log(id, key);
        }
    }
};
</script>
